.subscribe {
  display: inline-block;
  padding: 1.1rem;
  position: relative;
  left: 5rem;
  top: -1rem;
}

h2 {
  font-family: "Inter";
  font-style: normal;
  color: #282828;
  padding-bottom: 1rem;
}

form input {
  background: #ffffff;
  border: 4px solid #000000;
  border-radius: 8px;
  height: 3rem;
  width: 19rem;
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 160%;
  padding-left: 1.2rem;
}

form button {
  position: relative;
  left: 1rem;
  padding: 14px 36px;
  background: #282828;
  border: none;
  border-radius: 8px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  text-align: center;

  color: #ffffff;
}

.subscribe p {
  position: relative;
  left: 10%;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 160%;

  color: #ffffff;
}
